import React from "react"
import Container from 'react-bootstrap/Container'
import {Jumbotron, Row, Col} from "react-bootstrap"

import RealNavbar from "../components/real_navbar"

export default function Resume() {
  return (
    <div>
      <link
        rel="stylesheet"
        href="https://maxcdn.bootstrapcdn.com/bootstrap/4.5.0/css/bootstrap.min.css"
        integrity="sha384-9aIt2nRpC12Uk9gS9baDl411NQApFmC26EwAOH8WgZl5MYYxFfc+NcPb1dKGj7Sk"
        crossorigin="anonymous"
      />

      <RealNavbar/>
       <Container fluid>
        <br/>
        <Jumbotron>
          <Row>

            <Col>
            <h2>About</h2>
              <p>
                I'm Gabe - a software engineer and game designer. <br/>
                My interests primarily lay in game engine systems and computer graphics. <br/><br/>
           	
                I recently accepted a position at <b>Electronic Arts (EA)</b> as a <b>Systems Software Engineer</b> on Madden.<br/>
                I look forward to starting August 2022! <br/><br/>

				        In 2020 I graduated from Swarthmore College with a B.A. in Computer Science. <br/> After graudation
				        I worked as a Software Engineer at Northrop Grumman where I developed aeronautics software
				        using C++.<br/><br/>

				        Now, I'm working toward my M.S. in Interactive Entertainment (Programming Track) at the Florida Interactive Entertainment Academy (UCF FIEA).
              </p>
            </Col>
          </Row>

        </Jumbotron>

     

      </Container>


      <br/>
    </div>
  )
}
